.wrong {
  background-color: #be5569;
}

.selected {
  background-color: #d9e1e7;
  color: #121b21;
}

.correct {
  background-color: #9dc79d;
  color: #121b21;
}

.highlight-correct{
    color: #9dc79d;
}

.highlight-incorrect{
    color: #be5569;
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 100vh;
  padding: 20px;
  margin: 0 auto;
  background-image: linear-gradient(to top, #09203f 0%, #537895 100%);
}

.content {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 700px;
  padding: 20px;
  background-color: transparent;
  border-radius: 8px;
}

.input-section {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.input-section input {
  flex: 1;
  padding: 10px;
  border: 1px solid #ccc;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  border-radius: 4px;
  font-size: 16px;
  outline: none;
}

.input-section button {
  margin-left: 10px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  background-color: #fff;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 17px;
}

.input-section .timer {
  color: #dadada;
  margin-left: 10px;
  font-size: 24px;
}

p {
  color: #dadada;
  font-size: 24px;
  line-height: 1.5;
  margin: 10px 0;
}

@media (min-width: 768px) {
  .content {
    flex-direction: column;
    justify-content: center;
  }

  .input-section {
    flex-direction: row;
    align-items: center;
  }

  .input-section input {
    flex: 1;
    margin-right: 10px;
  }

  .input-section button {
    width: auto;
    margin: 0;
  }
}
